.btn{display:inline-block;border:solid 2px transparent;border-radius:7px;padding:15px 27px;cursor:pointer;transition:all .3s ease;text-decoration:none;font-weight:400}.btn-light{background-color:#f0f0f0;color:#1D1D1D}.btn-light:hover{background-color:#fff}.btn-outline-light{border-color:#f0f0f0;color:inherit}.btn-outline-light:hover{background-color:#f0f0f0;color:#0d0d0d !important}.btn-dark{background-color:#1D1D1D;color:#f0f0f0}.btn-dark:hover{background-color:#0d0d0d}.btn-outline-dark{border-color:#1D1D1D;color:inherit}.btn-outline-dark:hover{background-color:#1D1D1D;color:#f0f0f0}body{margin:0;padding:0;height:100%;font-family:'Lato', sans-serif}a{color:inherit !important}#hero{position:absolute;width:100%;height:100%;color:#fff;overflow-y:scroll;background:#1D4350;transition:all .4s}#hero.canvas-gradient-light{color:#000}@media (min-width: 768px){#hero{overflow-y:hidden}}#hero #hero-content{position:relative;z-index:100;display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:100vh}#hero #title{margin-top:auto;max-width:80%}@media (min-width: 768px){#hero #title{max-width:70%}}#hero #title h1{font-size:1.5em;font-weight:400;text-transform:uppercase}#hero #countdown{display:flex;flex-direction:column;margin:40px 0;transition:all 0.8s cubic-bezier(0.86, 0, 0.07, 1);font-size:1em}@media (min-width: 576px){#hero #countdown{flex-direction:row}}#hero #countdown.deconfined{font-size:.5em;margin-bottom:0}#hero #countdown .cd-group{display:flex;margin-right:30px;align-items:end}#hero #countdown .cd-group:last-child{margin-right:0}#hero #countdown .cd-number{font-size:5em;font-weight:300}#hero #countdown .cd-number.down.in{animation:in-down 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}#hero #countdown .cd-number.down.out{animation:out-up 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53) both}#hero #countdown .cd-number.up.in{animation:in-up 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}#hero #countdown .cd-number.up.out{animation:out-down 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53) both}@keyframes in-up{from{transform:translate3D(0, 20px, 0);opacity:0}}@keyframes out-up{to{transform:translate3D(0, 20px, 0);opacity:0}}@keyframes in-down{from{transform:translate3D(0, -20px, 0);opacity:0}}@keyframes out-down{to{transform:translate3D(0, -20px, 0);opacity:0}}#hero #countdown .cd-letter{font-size:3em;font-weight:700;opacity:0.5}#hero #deconfined-message{font-size:0;opacity:0;transition:font-size .5s, opacity .3s .5s;max-width:80%;text-align:center}@media (min-width: 768px){#hero #deconfined-message{max-width:70%}}#hero #deconfined-message.deconfined{opacity:1;font-size:1em}#hero #deconfined-message h1{font-size:3.3em;margin-top:25px;font-weight:300}#hero #more-info{text-align:center;margin-top:auto;max-width:80%;font-weight:300;font-size:1.1em}@media (min-width: 768px){#hero #more-info{max-width:70%}}#hero #credits{margin-bottom:10px;font-weight:300;max-width:80%}@media (min-width: 768px){#hero #credits{max-width:70%}}#canvas-gradient{display:block;position:fixed;width:100%;height:100vh;top:0;left:0;z-index:1}
